export default{
    namespaced : true,

    state: {
        count : 0
    },
    getters:{
        //해당 모듈은 root가 아니므로 state는 counter을 참조하고 rootState는 index.js의 state를 참조
        getCount(state, getters, rootState, rootGetters){
            return state.count;
        }
    },
    mutations:{
        setCount(state, payload){
            state.count += payload;
        }

        /*
        setCount : function(state, payload){
        }
        //쓰지마! ->  여기서 this를 쓰게되면 이 객체를 참조하지 않음
        setCount : (state, payload) => {

        }
        */

    },

    actions:{
        //payload : {value:1, duration:3000}
        setCountByAsync(context, payload){
            new Promise((resolve, reject) => {
                setTimeout(() => {
                    //성공적 응답
                    resolve(payload.value);
                }, payload.duration)
            })
            .then((data) => {
                //resolve 가 되었을 경우 mutaion을 이용해서 상태값 변경
                context.commit("setCount", data);//모듈 안에 존재하기 때문에 Counter/setCount 할 필요 없음 
                                                    //-> namespace를 사용했기 때문에 외부에서는 /  써야함
                console.log("count 상태 변경 성공");
            })
            .catch((error) => {

            })

        }
    }

}