//뷰 임포트
import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import menu01 from "./menu01";
import menu02 from "./menu02";
import menu03 from "./menu03";
import menu04 from "./menu04";
import menu05 from "./menu05";
import menu06 from "./menu06";
import menu07 from "./menu07";

const routes = [ //배열형식으로 라우트 정의 생성
  //생성시 바로 view를 다운로드
  {
    path: '/', //라우트 URL
    name: 'home', //라우트 이름
    component: HomeView //보여줄 View 컴포넌트
  },
  //필요시 view를 다운로드 하여 초기 속도 향상
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  ...menu01,
  ...menu02,
  ...menu03,
  ...menu04,
  ...menu05,
  ...menu06,
  ...menu07,
]

const router = createRouter({ //라우터 인스턴스 생성
  history: createWebHistory(process.env.BASE_URL), //URl 전환 시 history 객체 사용 (#노출 안됨)
  routes
})

export default router//외부 생성
