import axios from "axios";
import store from "@/store";
//useStore 는 컴포넌트 안에서만 사용할 수 있는 툴이라 못씀

//요것이 통신모듈이다

//회원가입
async function join(user) {
  try {
    const response = await axios.post("/member/join", {
      mid: user.id,
      mname: user.name,
      mpassword: user.password,
      mrole: user.role,
      memail: user.email,
    });
    return response.data.result; //response.data = map[json]
  } catch (error) {
    console.log(error);
    return "fail-network";
  }
}
//로그인
async function login(user) {
  try {
    const response = await axios.post("/member/login", {
      mid: user.id,
      mpassword: user.password,
    });
    store.dispatch("saveAuth", { userId: response.data.mid, authToken: response.data.accessToken });
    return "success";
  } catch (error) {
    if (error.response) {
      if (error.response.status == 401) {
        return "fail-401";
      }
    } else {
      //error.response 없으면 네트워크 에러
      return "fali-network";
    }
  }
}
async function logout() {
  try {
    await axios.get("/member/logout"); //서버쪽 정리
    store.dispatch("deleteAuth");
  } catch (error) {
    console.log(error);
  }
}

async function refreshToken(){
  if(store.state.authToken !== ""){
    try{
      const response = await axios.get("/member/refreshToken");
      store.dispatch("saveAuth", {
        userId : response.data.mid,
        authToken : response.data.accessToken
      });
      return true;
    }catch(error){
      console.log(error);
    }
  }
  return false;
}
export default {
  join : join,
  login : login,
  logout : logout,
  refreshToken : refreshToken
};
